.customer-recs-container {
  background-color: $clr9;

  .customer-recs {
    max-width: 567px;
    margin: 0 auto;
    padding: 140px 24px 0;
    height: 1410px;
    @include for-wide-layout {
      max-width: 1440px;
      padding: 140px 165px 0;
      height: 745px;
    }
    h1 {
      /* Style for "Delivering" */
      width: 327px;
      height: 129px;
      font-family: livvic-bold;
      font-size: rem(32px);
      line-height: 32px;
      text-align: center;
      @include for-mobile-layout {
        margin: 0 auto 88px;
      }
      @include for-wide-layout {
        margin-bottom: 88px;
        width: 932px;
        height: 100px;
        font-size: rem(48px);
        line-height: 48px;
      }
      span {
        color: $clr4;
      }
    }

    .recos-list {
      gap: 30px;
      @include for-mobile-layout {
        gap: 84px;
        flex-direction: column;
        align-items: center;
      }
    }
  }
}
