.reco-preview {
  position: relative;
  width: 327px;

  @include for-wide-layout {
    width: 350px;
  }
  flex-direction: column;
  justify-content: center;
  text-align: center;
  .quote-img-container {
    position: absolute;
    top: -40px;
    left: 50%;
    transform: translateX(-50%);
    width: 67px;
    height: 56px;
    z-index: 0;
  }

  .reco-info {
    z-index: 1;
    p {
      height: 94px;
      font-size: 0.937rem;
      line-height: 25px;
      margin-bottom: 22px;
      @include for-wide-layout {
        margin-bottom: 26px;
      }
    }

    h4 {
      height: 28px;
      color: $clr4;
      font-family: livvic-bold;
      font-size: rem(18px);
      line-height: 28px;
    }

    small {
      height: 18px;
      font-family: livvic-medium-italic;
      font-size: rem(13px);
      line-height: 18px;
    }

    .person-img-container {
      margin: 16px auto 0;
      width: 62px;
      height: 62px;
      @include for-wide-layout {
        margin: 32px auto 0;
      }
      img {
        border: 2px solid #c4fffe;
        border-radius: 50%;
      }
    }
  }
}
