.main-hero-container {
  background-color: $clr1;
  .main-hero {
    margin: 0 auto;
    max-width: 375px;
    height: 439px;
    position: relative;
    padding: 32px 24px 0;
    color: #ffffff;
    @include for-mobile-layout {
      text-align: center;
      flex-direction: column;
    }
    @include for-wide-layout {
      justify-content: space-between;
      padding: 56px 165px 0;
      height: 594px;
      max-width: 1440px;
    }

    h1 {
      font-family: livvic-bold;
      font-size: rem(40px);
      line-height: 40px;
      padding: 0 40px;
      width: 327px;
      height: 81px;
      @include for-mobile-layout {
        margin-bottom: 17px;
      }
      @include for-wide-layout {
        font-size: rem(100px);
        line-height: 100px;
        padding: 0;
        padding-right: 20px;
        width: 635px;
        height: 201px;
      }
      span {
        color: #f67e7e;
      }
    }

    .info-container {
      width: 327px;
      @include for-wide-layout {
        width: 445px;
      }
      .sep {
        @include for-mobile-layout {
          display: none;
        }
        width: 50px;
        height: 4px;
        background-color: #79c8c7;
        margin-bottom: 80px;
      }

      p {
        @include for-mobile-layout {
          font-size: rem(15px);
        }
        width: inherit;
        height: 109px;
        font-family: livvic-semi-bold;
        line-height: 28px;
      }
    }

    .img-container {
      width: 358px;
      position: absolute;
      bottom: -105px;
      right: 0px;
      
      @include for-wide-layout {
        right: 165px;
      }
    }
  }
}
