.app-footer-container {
  background-color: $clr7;
  width: 100%;
  min-width: 1440px;
  .app-footer {
    height: 196px;
    max-width: 1110px;
    margin: 0 auto;
    align-items: center;
    justify-content: space-between;

    .logo-container {
      height: 93px;
      .logo {
        display: block;
        width: 160px;
        margin-bottom: 25px;
      }
      p:first-child {
        margin-inline-end: 40px;
      }
      p {
        display: inline-block;
        width: 49px;
        font-size: rem(18px);
        line-height: 28px;
      }
    }

    .contact-info {
      width: 445px;
      opacity: 0.6;
      font-size: rem(15px);
      p {
        line-height: 25px;
      }
    }

    .social-rights {
      .social-btns {
        height: 24px;
        margin-bottom: 50px;
        text-align: right;
        img {
          width: 24px;
          height: 24px;
          &:not(:last-child) {
            margin-inline-end: 16px;
          }
        }
      }
      p {
        width: 255px;
        opacity: 0.6;
        font-size: rem(15px);
        line-height: 25px;
      }
    }
  }
}
