* {
  box-sizing: border-box;
}

::selection {
  background-color: $clr1;
  color: $clr6;
}

html {
  font-family: livvic-semi-bold;
  color: $clr6;
}

body {
  margin: 0;
  font-size: rem(18px);
}

h1,
h2,
h3,
h4,
h5,
h6 {
  margin: 0;
}

img {
  width: 100%;
}

p {
  margin: 0;
}

a {
  text-decoration: none;
}
a:hover {
  text-decoration: underline;
}

button {
  color: inherit;
  cursor: pointer;
}
