.main-container {
  display: grid;
  grid-template-columns: 24px 1fr 24px;
  @include for-wide-layout {
    grid-template-columns: 1fr 1440px 1fr;
  }
  > * {
    grid-column: 2;

    &.full {
      grid-column: 1 /-1;
    }
  }

