.contact-us-container {
  background-color: $clr3;
  position: relative;
  .img-container {
    position: absolute;
    bottom: -5px;
    left: 0;
  }
  .contact-us {
    margin: 0 auto;
    max-width: 1440px;
    height: 200px;
    align-items: center;
    justify-content: center;
    .text-container {
      color: $clr2;
      width: 917px;
      justify-content: space-between;
      align-items: center;

      h2 {
        width: 504px;
        height: 53px;
        font-family: livvic-Bold;
        font-size: rem(48px);
        line-height: 48px;
      }

      button {
        font-family: livvic-semi-bold;
        background: inherit;
        width: 153px;
        height: 48px;
        border-radius: 24px;
        border: 2px solid $clr2;
        font-size: rem(18px);
        line-height: 28px;
      }
    }
  }
}
