.header-container {
  background-color: $clr1;
  min-width: 90%;
  @include for-wide-layout {
    min-width: 1440px;
  }
  .app-header {
    max-width: 1440px;
    margin: 0 auto;
    height: 128px;
    color: $clr6;
    font-family: livvic-semi-bold;
    line-height: 28px;
    padding: 0 24px;
    align-items: center;
    font-size: rem(18px);
    @include for-wide-layout {
      height: 194px;
      padding: 0 165px;
    }

    .logo {
      width: 128px;
      height: 32px;

      @include for-wide-layout {
        width: 160px;
        height: 40px;
        margin-inline-end: 40px;
      }
    }

    .nav-bar {
      display: none;
      @include for-wide-layout {
        display: flex;
        flex: 1;
        justify-content: space-between;

        .nav-btns {
          margin: 0 40px;
          gap: 40px;
          align-items: center;
        }

        .contact-btn {
          font-size: rem(18px);
          font-family: livvic-semi-bold;
          width: 153px;
          height: 48px;
          border-radius: 24px;
          border: 2px solid $clr6;
          background-color: inherit;
        }
      }
    }
  }
}
