.main-info-container {
  background-color: $clr2;
  position: relative;
  
  .main-info {
    position: relative;
    max-width: 1440px;
    margin: 0 auto;
    height: 684px;
    padding: 200px 165px 0;
    .sep {
      width: 50px;
      height: 4px;
      background-color: $clr3;
      position: absolute;
      left: 165px;
      top: 140px;
    }

    h1 {
      width: 445px;
      font-family: livvic-bold;
      font-size: 48px;
      margin-inline-end: 130px;
    }

    .skill-list {
      width: 540px;
      display: flex;
      flex-direction: column;
      gap: 35px;
    }
  }
  .img-container {
    position: absolute;
    bottom: 0;
    right: 0;
  }
}
